import { Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CrudService } from 'src/app/services/crud.service';
import { cusRequestUrl } from 'src/app/services/customerReqUrl.const';

@Component({
  selector: 'app-viewproduct',
  templateUrl: './viewproduct.component.html',
  styleUrls: ['./viewproduct.component.scss']
})
export class ViewproductComponent {
  productId:any;
  productDetail:any;
constructor(private route:ActivatedRoute, private crud:CrudService){

}

ngOnInit(): void {

  this.productId = this.route.snapshot.paramMap.get('id');

  this.crud.getRequest(`${cusRequestUrl.getProductDetail}${this.productId}`).then((res:any)=>{
    this.productDetail=res.data;
})
}
}
